//   IE9+
input[type=checkbox],
input[type=radio] {
  display: none;
}

i.checkbox {
  font-size: 20px;
}

// checkbox
i.checkbox:before,
i.radio:before {
  content: "\E603";
  // color: #00fffe;
}

// checkbox
input[type=checkbox]:checked + i:before,
input[type=radio]:checked + i:before {
  content: "\e655";
  // color: #00fffe;
}



